html:not(.header-toggled-state) .header .header-logo-link.lottie-anim-init .header-logo-img {
  opacity: 1;
  visibility: unset !important;
}

html.header-toggled-state .header-logo-img.header-logo-white.dont-show.non-gif {
  opacity: 1 !important;
  visibility: unset !important;
}
html.header-toggled-state .header .header-open-form-btn {
  pointer-events: unset;
  visibility: visible;
  opacity: 1;
}

.hbspt-form.sk-form-hbsp .hs_submit.hs-submit .actions {
  display: flex;
  justify-content: space-between;
  align-content: center;
  align-items: center;
}
.hbspt-form.sk-form-hbsp .hs_submit.hs-submit .actions .img-srtcode {
  max-height: 55px;
  display: block;
}
@media screen and (max-width: 1200px) {
  .hbspt-form.sk-form-hbsp .hs_submit.hs-submit .actions .img-srtcode {
    max-height: 35px;
  }
}